<template>
    <v-card flat fill-height style="height:100%;width:100%;">
        <v-card-title  primary class="title" style="height:10%;width:100%;">Chart Title</v-card-title>
        <v-card-actions fill-height style="height:90%;width:100%;">  
            <highcharts style="height:100%;width:100%;" v-show="load" :options="chartOptions"></highcharts>
        </v-card-actions>
    </v-card>
</template>

<script>

    import {Chart} from 'highcharts-vue'
    import More from 'highcharts/highcharts-more'
    import Highcharts from 'highcharts'

    More(Highcharts)

    export default {
        name: "BarChart",
        components: {
             highcharts: Chart 
        },
        props: {
            chartData:Object
        },
        data: function () {
            return {
                load:false,                
                chartOptions: {
                    legend:{
                        enabled:false
                    },
                    title:{
                        text:undefined
                    },
                    series: [{                        
                        type:'column',
                        data:[]
                    }]
                }
            }
        },
        methods: {
        },
        created() {

            for (let i=1;i<7;i++)
            {
                let arg = i;
                let val = Math.random()*150;

                
                let min = Math.random()*150;
                let max = min + Math.random()*(150-min);

                let color = 'rgb(60, 171, 48)';

                if ((val<min)||(val>max)) color = 'rgb(205, 57, 64)';

                this.chartOptions.series[0].data.push({
                    x:arg,
                    y:val,
                    color:color
                });

            }

            this.chartOptions.series[0].data.sort(function (a, b) {
                return  a.x > b.x ? 1 : -1;
            })

            this.load= true;

        }, 
        watch:
        {
        }
    }
</script>

