                                                v-bind:title="'BSFC'"/>
                                        </v-flex>
                                        <v-flex d-flex md6>                                            
                                            <Gauge
                                                v-bind:value="168"
                                                v-bind:rangeLimit="200"
                                                v-bind:startValue="140"
                                                v-bind:endValue="220"
                                                v-bind:tickInterval="20"
                                                v-bind:format="2"
                                                v-bind:unit="'[gr/kWh]'"
                                                v-bind:title="'BSFC'"/>
                                        </v-flex>
                                    </v-layout>                                    
                                </v-card-actions>
                            </v-card>
                        </v-flex>
                        <v-flex d-flex md3>
                            <EngineState/>
                        </v-flex>
                        <v-flex d-flex md6>
                            <SubsystemState/>
                        </v-flex>
                    </v-layout>
                </v-flex>
                <v-flex d-flex md6  style="max-height:50%;">
                    <v-layout row>
                        <v-flex d-flex md6>
