export default {
  name: "VesselView",
  components: { 
      Map,
      VesselInfo,
      SubsystemState,
      EngineState,
      PerformanceWidget,
      FaultsWidget,
      Gauge
   },
   data: function () {
        return {  
            cardData:
            {
                'pcomp':{
                    value:96.52,
                    ref:98.42,
                    title:'Compression Pressure',
                    unit:'bar',
                    color:0
                },
                'tcSpeed':{
                    value:17877,
                    ref:16850,
                    title:'T/C speed',
                    unit:'rpm',
                    color:20
                },
                'pmax':{
                    value:128.7,
                    ref:131.64,
                    title:'Firing Pressure',
                    unit:'bar',
                    color:0}
            } 
        }
    },
    mounted() {
        if (globalStore.selectedVessel=='') 
                this.$router.push( { path:  'dashboard'} );
    }
}
</script>

