<template>
    <v-card flat style="height:100%;width:100%">
        <v-card-title  primary class="title" style="height:10%;">{{title}}</v-card-title>
        <v-card-actions fill-height style="height:90%;">  
            <dx-circular-gauge   style="width:100%;height:100%;"
                            :value="value"
                            :redraw-on-resize="true" >
                <dx-value-indicator
                    spindleGapSize=0
                    type="triangleNeedle"
                    width=10
                    spindleSize=10
                    color="rgb(33,33,33)"
                />
                <dx-geometry
                    :start-angle="225"
                    :end-angle="315"
                />
                <dx-scale
                    :start-value="startValue"
                    :end-value="endValue"
                    :tick-interval="tickInterval">
                    <dx-label :use-range-colors="true" />
                    <dx-tick :length="15" />
                </dx-scale>
                <dx-range-container :width="15" background-color="transparent">
                <dx-range :start-value="startValue"
                            :end-value="rangeLimit"
                            color= "rgb(60, 171, 48)"/>                        
                <dx-range :start-value="rangeLimit"
                            :end-value="endValue"
                            color="rgb(205, 57, 64)"/>
                </dx-range-container> 
                <dx-title
                    :text="value.toFixed(format)+' '+unit"
                    vertical-alignment="bottom"
                    horizontal-alignment="center">
                    <dx-font
                        size=14
                    />
                </dx-title>           
            </dx-circular-gauge>   
        </v-card-actions>
    </v-card>
</template>

<script>

  import { DxCircularGauge,
    DxSize,
    DxValueIndicator,
    DxGeometry,
    DxScale,
    DxRangeContainer,
    DxRange,
    DxTitle,
    DxFont,
    DxLabel,
    DxTick
  } from "devextreme-vue/circular-gauge";

export default {
    name: 'Gauge',
    components: {
        DxCircularGauge,
        DxSize,
        DxValueIndicator,
        DxGeometry,
        DxScale,
        DxRangeContainer,
        DxRange,
        DxLabel,
        DxTick,
        DxTitle,
        DxFont
    },
    props:
    {
        value: Number,
        rangeLimit: Number,
        startValue : Number,
        endValue : Number,
        tickInterval : Number,
        unit : String,
        format : Number,
        title: String
    },
    data () {
        return {
        }
    },
    computed: {    
    },
    watch: {
        
    },
    methods:
    {            
    }
}
</script>

