<template>
    <v-toolbar fixed flat id="titleBar" :height=40 style="top:60px;">
        <div v-for="item in breadCrumbList" v-bind:key="item">        
            <div class="titleBtn" v-if="item!='Dashboard'">></div>
            <v-btn flat class="titleBtn" v-if="item=='Vessel View'" v-on:click="navTo(item)">{{selVessel}}</v-btn>
            <v-btn flat class="titleBtn" v-else v-on:click="navTo(item)"> {{item}}</v-btn>
        </div>
    </v-toolbar>
</template>

<script>

import {globalStore} from "../main.js"

export default {
    name: 'Titlebar',
    components: {
    },
    props:
    {
    },
    data () {
        return {
            breadCrumbList:['Dashboard']
        //
        }
    },
    computed: {    
        currPath: function () {
            return  this.$route.path;
        },        
        selVessel: function () { return globalStore.selectedVessel; }
    },
    watch: {
        currPath : function() {

            let name = this.$route.name;

            if (this.breadCrumbList.includes(name))
            {
                let index = this.breadCrumbList.indexOf(name);

                this.breadCrumbList = this.breadCrumbList.slice(0,index+1);

            }
            else 
            {
                this.breadCrumbList.push(name);
            }

        }
    },
    methods:
    {        
        navTo(screen)
        {
            this.$router.push( { name: screen  } )
        }        
    }
}
</script>

<style scoped>
#titleBar
{
  background-color:rgb(244,244,244);
  padding:0px;  
  font-size: 24px;
  color:rgb(51,82,128);
}

.titleBtn
{
  color:rgb(51,82,128);
  font-size:20px;
  text-transform: none;     
  display: inline-block;
}

</style>