<template>
    <v-container fluid fill-height grid-list-md style="padding:5px;">
        <v-layout column fill-height>            
            <v-flex d-flex md2 style="align-items:center;justify-content:center;font-size:36px;">
                {{trendData.value.toFixed(trendData.format)}}                   
            </v-flex>
            <v-flex d-flex md8>
                <highcharts style="height:100%;width:100%;" :options="chartOptions"></highcharts>
            </v-flex>
            <v-flex d-flex md2 style="align-items:center;justify-content:center;font-size:16px;">
                {{trendData.title}} [{{trendData.unit}}]
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>

    import {Chart} from 'highcharts-vue'

    export default {
        name: "Trendline",
        components: {
            highcharts: Chart 
        },
        props: {
            trendData:Object
        },
        data: function () {
            return {   
                chartOptions: {
                    chart:{
                        backgroundColor:'transparent'
                    },
                    legend:{
                        enabled:false
                    },
                    title:{
                        text:undefined
                    },
                    xAxis:
                    {
                        labels:{
                            enabled:false
                        }
                    },
                    yAxis:
                    {
                        labels:{
                            enabled:false
                        },
                        title:{
                            text:undefined
                        }
                    },
                    series: [{ 
                        name:'Values',                       
                        type:'line',
                        data:[[1,23], [4,87], [9, 47],[12, 56]],
                        marker:{
                            enabled:false
                        }
                    },
                    {
                        name:'Reference',
                        type:'line',
                        data:[[1,34], [4,67], [9,43], [12,65]],
                        marker:{
                            enabled:false
                        }
                    }]
                }
            }
        },
        methods: {
        },
        mounted() {
        }, 
        watch:
        {
        }
    }
</script>

